import Vue from 'vue'
import Router from 'vue-router'
import Topic from './views/Topic.vue'
import News from './views/News.vue'
import Tech from './views/Tech.vue'
import Blockchain from './views/Blockchain.vue'
import Jobs from './views/Jobs.vue'
import Toolbar from "@/components/Toolbar"
import Error from  '@/views/Error'
import Details from  '@/views/Details'

Vue.use(Router);

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path:'/',
            redirect: '/topic'
        },
        {
            path: '/topic',
            name: 'topic',
            meta: { title: 'Readhub'},
            components: {
                default:Topic,
                toolbar:Toolbar
            }
        },
        {
            path: '/news',
            name: 'news',
            meta: { title: 'Readhub - ' },
            components: {
                default:News,
                toolbar:Toolbar
            }
        },
        {
            path: '/tech',
            name: 'tech',
            meta: { title: 'Readhub - ' },
            components: {
                default:Tech,
                toolbar:Toolbar
            }
        },
        {
            path: '/blockchain',
            name: 'blockchain',
            meta: { title: 'Readhub - ' },
            components: {
                default:Blockchain,
                toolbar:Toolbar
            }
        },
        {
            path: '/jobs',
            name: 'jobs',
            meta: { title: 'Readhub - ' },
            components: {
                default:Jobs,
                toolbar:Toolbar
            }
        },{
            path: '/topic/:id',
            name: 'details',
            components: {
                default:Details,
                toolbar:Toolbar
            }
        },{
            path: '*',
            name: 'error',
            meta: { title: '404-'},
            components: {
                error:Error
            },
        },
    ]
});

router.beforeEach(
    (to, from, next) => {
        to.meta.title && (document.title = to.meta.title);
        next()
    }
);

export default router